import React from 'react'
import { Route, Switch, Link } from 'react-router-dom'

const App = () =>
  <Switch>
    <Route path='/subpage'>
      <SubPage />
    </Route>
    <Route path='/'>
      <HomePage />
    </Route>
  </Switch>

const HomePage = () => <div>
  This is the homepage. <Link to='/subpage'>Click here</Link> for the subpage.
</div>

const SubPage = () => <div>
  This is the subpage. <Link to='/'>Click here</Link> for the homepage.
</div>

export default App